<template>
    <div class="add-brand">
    <base-header type="gradient" class="pb-7 pt-7"> </base-header>
    <div class="container-fluid mt--7">
      <div class="row no-gutters justify-content-center">
        <div class="col-lg-8 mt-2">
          <span>
            <i class="fas fa-chevron-left text-info" style="outline: 0;box-shadow: none!important;">
            </i>
            <router-link :to="{ name: ViewSection }"  class="pl-2 text-info form-control-label" style="font-size: 16px;">Back</router-link>
          </span>
        </div>
      </div> 


      <div class="row no-gutters justify-content-center">
        <div class="col-lg-8 col-md-8">

          <div class="card shadow bg-secondary mb-6">
            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row align-items-center">
                  <div class="col-12">
                    <h3 class="mb-0">View/Update Section</h3>
                  </div>
                </div>
              </div>
            </div>
          <form> 
            <div class="card-body">
                <div class="">
                  <div class="row">
                    <div class="col-6">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             Title
                          </label>
                          <select class="form-control form-control-alternative" v-model="section_title">
                            <option disabled value="">Please select one</option>
                            <option
                              v-for="item in Object.keys(section_datas)"
                              :key="item"
                              :value="item"
                            >
                              {{ section_datas[item] }}
                            </option>
                          </select>
                       </div>
                    </div>
                    <div class="col-6">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             Section Name
                          </label>
                          <select class="form-control form-control-alternative" v-model="section_name">
                            <option disabled value="">Please select one</option>
                            <option
                              v-for="item in Object.keys(section_names)"
                              :key="item"
                              :value="item"
                            >
                              {{ section_names[item] }}
                            </option>
                          </select>
                       </div>
                    </div>
                  </div>
                </div>
                <div class="">
                  <div class="row">
                    <div class="col-6">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             Section Type
                          </label>
                          <select class="form-control form-control-alternative" v-model="section_type">
                            <option disabled value="">Please select one</option>
                            <option
                              v-for="item in Object.keys(section_types)"
                              :key="item"
                              :value="item"
                            >
                              {{ section_types[item] }}
                            </option>
                          </select>
                       </div>
                    </div>
                    <div class="col-6">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             Status
                          </label>
                          <select class="form-control form-control-alternative" v-model="section_status">
                            <option selected value=true>Active</option>
                            <option value=false>Inactive</option>
                          </select>
                       </div>
                    </div>
                  </div>
                </div>
                <div class="">
                    <div class="row">
                      <div class="col-4">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             Section Image
                          </label>
                          <div class="card">
                            <div v-if="image_url">
                              <img class="card-img-top" :src="image_url" alt="image ..1"/>
                            </div>
                            <div v-else>
                              <img class="card-img-top" src="https://st4.depositphotos.com/14953852/24787/v/600/depositphotos_247872612-stock-illustration-no-image-available-icon-vector.jpg" alt="image ..1"/>
                            </div>
                            <div class="card-footer">
                              <input hidden="true" ref="file" type="file" v-on:change="handleImageUpload()" accept="image/*"/>
                              <button v-if="!image_url" class="btn btn-primary btn-sm" type="button" @click="$refs.file.click()">Add</button>
                              <button v-else class="btn btn-primary btn-sm" type="button" @click="$refs.file.click()">Change</button>
                              <button :disabled="!image_url" class="btn btn-info btn-sm" type="button" @click="removeImage()">Remove</button>
                            </div>
                          </div>
                       </div>
                    </div>
                      <div class="col-8">
                        <label class="form-control-label">
                            Section data url
                        </label>
                        <input 
                          class="form-control"
                          v-model="section_data_url"
                          readonly="readonly"
                        />
                      </div>
                    </div>
                </div>
            </div>

            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row">
                  <div class="col-12 text-right">
                    <button type="button" class="btn btn-danger mr-2" @click="deleteSection()">Delete</button>
                    <button type="button" class="btn btn-secondary mr-2" @click="cancelEve()">Cancel</button>
                    <button type="button" class="btn btn-info" @click="updateSection()">Update Section</button>
                  </div>
                </div>
              </div>
            </div>
          </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
  input, select {
    color: rgba(122, 23, 117, 0.616);
    font-weight: bold;
  }
  .card-img-top{
    max-height: 172px;;
  }
  .card-img-top{
    min-height: 172px;;
  }
</style>
<script>
import axios from "axios";
axios.defaults.xsrfHeaderName = "X-CSRFToken";
export default {
    name: "view Section",
    props: {
      hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST
      },
      source: {
        type: String,
        default:"modicare"
      }
    },
    data(){
        return{
            image_url:'',
            section:{},
            section_datas:[],
            section_types:[],
            section_names:[],
            section_title:'',
            section_type:'',
            section_name:'',
            section_data_url:'',
            section_status:true,
            ViewSection:"",
        }
    },
    async created(){
      this.section_datas = this.$store.state.section_datas;

      this.section_types = this.$store.state.section_types;

      this.section_names = {...this.$store.state[this.source.toLowerCase()+"_section"], ...this.$store.state.section_names}
    },
    async mounted() {
        if(this.source === "modicare"){this.ViewSection = "Section";}else{this.ViewSection = this.source+"Section"}
        let section_id = this.$route.params.id;
        let section_data = await axios.get(this.hostApi+`/api/section/${section_id}/`)
        this.section_title = section_data.data.title;
        this.section_name = section_data.data.section_name;
        this.section_type = section_data.data.section_type;
        this.section_status = section_data.data.status;
        this.image_url = section_data.data.section_image || '';
        this.section_data_url = section_data.data.section_data;
    },
    methods: {
        handleImageUpload(){
         let uploadedImage = this.$refs.file.files;
         this.image_url = URL.createObjectURL(uploadedImage[0]);
        },
        removeImage(){
            this.image_url = "";
        },
        async updateSection(){
           let { section_title, section_name, section_type, section_status } = this;
          let formData = new FormData();
          if(this.$refs.file.files.length > 0){
            formData.append('section_image',this.$refs.file.files[0]);
          }
          formData.append('section_type',section_type);
          formData.append('title', section_title);
          formData.append('section_name',section_name);
          formData.append('status',section_status);
          let config = {
            method: 'patch',
            url: this.hostApi+"/api/section/"+this.$route.params.id+"/",
            headers: { 
                   'Content-Type': 'multipart/form-data'
                },
            data:formData,
            xsrfHeaderName: "X-CSRFToken",
          };
          let res = await axios(config);
          if(res.status == 200){
                this.$toast.success(`Section data updated successfully!`,{
                position: "top",
                canTimeout:true,
                duration:2500,
              });
              this.$router.push({name: this.ViewSection})
            }else{
              this.$toast.error(`Something went wrong`,{
              position: "top",
              canTimeout:true,
              duration:2500,
            });
        }
      },
      cancelEve(){
        this.$router.go(-1)
      },
      async deleteSection(){
        this.$swal({
            title: 'Are you sure?',
            text: "You won't be able to revert this!",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Yes, delete it!'
          }).then((result) =>{
            if(result.isConfirmed) {
                const config = {
                  method: 'delete',
                  url: this.hostApi +"/api/section/"+ this.$route.params.id+"/",
                  headers: {}
                };
                axios(config).then((res)=>{
                  console.log("res",res)
                  if(res.status == 204 || res.status == 200){
                    this.$toast.success(`Section data deleted successfully!`,{
                      position: "top",
                      canTimeout:true,
                      duration:2500,
                    });
                    this.$router.push({name: this.ViewSection})
                  }
                }).catch((e) =>{
                  this.$toast.error(e.message,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                  });
                })
              }
          })
      }
    }
}
</script>