<template>
    <div class="card shadow bg-secondary">
    <div class="card-header">
        <div class="bg-white border-0">
        <div class="row align-items-center">
            <div class="col">
            <h3 class="mb-0">Bill Generator</h3>
            </div>
        </div>
        </div>
    </div>
        <div class="card-body">
        <div class="">
            <div class="row">
            <div class="col-6">
                <div class="form-group has-label">
                <label class="form-control-label">
                    Min Products to require Coin
                </label>
                <input type="number" class="form-control" placeholder="Required Coins" name="required_coins" v-model="formData.min_items">
                </div>
            </div>
            <div class="col-6">
                <div class="form-group has-label">
                <label class="form-control-label">
                    No of Coin require for 1 product
                </label>
                <input type="number" class="form-control" placeholder="Min Products" name="min_items" v-model="formData.required_coins">
                </div>
            </div>
            </div>
        </div>
        </div>
        <div class="card-footer">
        <div class="bg-white border-0">
        <div class="row">
            <div class="col-12 text-right">
            <button type="button" class="btn btn-info" v-on:click="updateBillGenerator()">Update</button>
            <!-- <button type="button">Add</button> -->
            </div>
        </div>
        </div>
    </div>
    </div>
</template>
<script>
import axios from "axios";

export default {
    name: "BillView",
    props: {
      hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST
      },
      source: {
        type: String,
        default:"modicare"
      },
    },
    data() {
        return {
            formData : {
               required_coins: 0,
               min_items: 0
            },
            bill_id:''
        }
    },
    async mounted() {
        const BillData = await axios.get(this.hostApi+'/api/required-coin/')
        if(BillData.data.length > 0){
            this.formData = BillData.data[0];
            this.bill_id = BillData.data[0].id;  
        }
    }, 
    methods: {
        async updateBillGenerator(){
            if(!this.bill_id)return alert("Bill ID is missing!");
            const data = JSON.stringify(this.formData);
            const config = {
                method: 'put',
                url: this.hostApi+'/api/required-coin/'+this.bill_id+'/',
                headers: { 
                'Content-Type': 'application/json', 
                },
                data : data
            };
            const resp = await axios(config);
            if (resp.status === 200){
                this.$toast.success(`Bill Generator Data updated successfully`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
            }else{
                this.$toast.error(`Something went wrong`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
            }
        }
    }
}
</script>