import axios from "axios";

export const getUsersData = async (host) => {
    return await axios.get(host +'/api/app-user/');
}

export const getBvRequestData = async (host) => {
    return await axios.get(host +'/api/bv-admin-requests/');
}

export const getNextData = async (next_url) => {
    return await axios.get(next_url);
}

export const getPrevData = async (prev_url) => {
    return await axios.get(prev_url);
}

export const changeUserStatus = async (host, data, user_id) => {
    const headers = {
        'Content-Type': 'application/json'
    }
    return await axios.patch(`${host}/api/app-user/${user_id}/`, data, {headers});  
}

export const deleteUserPermanently = async (user_id, app_name) => {
    return await axios.delete(`${process.env.VUE_APP_SERVER_HOST}/v1/api/${user_id}/delete?appname=${app_name}`, )
}

export const searchingUser = async (host, search_text) => {
    return await axios.get(`${host}/api/app-user/?search=${search_text}`)
}

export const getDynamicTimer = async (host) => {
    return await axios.get(`${host}/api/dynamic-timer/`);
}

export const updateDynamicTimer = async (host, formData) => {
    const {id} = formData;
    delete formData['id']
    const data = JSON.stringify(formData);
    const headers = {
        'Content-Type': 'application/json'
    }
    return await axios.put(`${host}/api/dynamic-timer/${id}/`, data, {headers})
}