<template>
    <div
      class="card-footer d-flex justify-content-end"
      :class="type === 'dark' ? 'bg-transparent' : ''"
    >
       <ul class="pagination">
        <li class="text-right pr-3">
          <button
            type="button"
            :disabled="!prev"
            @click="getPrevData()"
            class="pagination-btn"
          >
            <i class="fas fa-chevron-left"></i>
          </button>
        </li>
        <li class="text-center" style="max-width: 55px">
          <span class="page-number-circle">{{ currentpage }}</span>
        </li>
        <li class="text-right pl-3">
          <button
            type="button"
            :disabled="!next"
            @click="getNextData()"
            class="pagination-btn"
          >
            <i class="fas fa-chevron-right"></i>
          </button>
        </li>
      </ul>

    </div>
</template>
<script>
export default {
    name: "TablePaginator",
    props: {
        type: {
            type: String,
        },
        title: String,
        hostApi:{
            type: String,
            default: process.env.VUE_APP_SERVER_HOST
        },
        source: {
            type: String,
            default: "modicare"
        },
        next:{
            type: String,
            default: null
        },
        prev: {
            type: String,
            default: null
        },
        currentpage: {
            type: Number,
            default: 1
        }
    },
    async mounted(){
        if(this.next){
            console.log(this.next)
        }
    },
    methods:{
        getNextData(){
          this.$emit("get-next-page", this.next);
        },
        async getPrevData(){
            this.$emit("get-prev-page", this.prev);
        }
    },
}
</script>