<template>
  <div>
    <base-header type="gradient" class="pb-6 pb-8 pt-5 pt-md-8">
    </base-header>
    <div class="container-fluid mt--7">
      <div class="row no-gutters justify-content-center">
        <div class="col-md-12">
          <adsense-table title="Light Table" :hostApi="hostApi" :source="source"></adsense-table>
        </div>
      </div>
      <br><br>
      <div class="row no-gutters justify-content-center">
        <div class="col-md-12">
          <div class="card shadow bg-secondary mb-6">
            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row align-items-center">
                  <div class="col">
                    <h3 class="mb-0">AdSense Count</h3>
                  </div>
                </div>
              </div>
            </div>
             <div class="card-body">
               <div class="">
                 <div class="row">
                    <div class="col-4">
                      <div class="form-group has-label">
                        <label class="form-control-label">
                          Product List Page
                        </label>
                        <input type="number" class="form-control" placeholder="Product Listing Page" name="product_list" v-model="product_list">
                      </div>
                    </div>
                    <div class="col-4">
                      <div class="form-group has-label">
                        <label class="form-control-label">
                          DP List Page
                        </label>
                        <input type="number" class="form-control" placeholder="DP Listing Page" name="dp_list" v-model="dp_list">
                      </div>
                    </div>
                    <div class="col-4">
                      <div class="form-group has-label">
                        <label class="form-control-label">
                          Bill List Page
                        </label>
                        <input type="number" class="form-control" placeholder="Bill Listing Page" name="bill_list" v-model="bill_list">
                      </div>
                    </div>
                 </div>
               </div>
             </div>
             <div class="card-footer">
              <div class="bg-white border-0">
                <div class="row">
                  <div class="col-12 text-right">
                    <button type="button" v-if="adsenseCountId" class="btn btn-info" @click="AdsenseCount('update')">Update</button>
                    <button type="button" v-else class="btn btn-info" @click="AdsenseCount('add')">Add</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import AdsenseTable from "./AdSenseTable.vue";
import axios from "axios";
export default {
  name: "tables",
  props: {
      hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST
      },
      source: {
        type: String,
        default:"modicare"
      }
    },
  components: {
    AdsenseTable,
  },
  data() {
    return {
      adsenseCountId: "",
      product_list:0,
      dp_list:0,
      bill_list:0,
    }
  },
  async mounted() {
    let adSenseCounts = await axios.get(this.hostApi+'/api/adsense-count/')
    this.product_list = adSenseCounts.data[0].product_list;
    this.dp_list = adSenseCounts.data[0].dp_list;
    this.bill_list = adSenseCounts.data[0].bill_list;
    this.adsenseCountId = adSenseCounts.data[0].id;
  },
  methods: {
    async AdsenseCount(action){
      try{
        var data = JSON.stringify({
        "product_list": this.product_list,
        "dp_list": this.dp_list,
        "bill_list": this.bill_list
      });

      var config = {
        method: 'post',
        url: this.hostApi+'/api/adsense-count/',
        headers: { 
          'Content-Type': 'application/json', 
        },
        data : data
      };

      if(action === 'update'){
        config['method'] = 'put';
        config['url'] = this.hostApi+'/api/adsense-count/'+this.adsenseCountId+'/';
      }

      let res = await axios(config);
      if(res.status == 200 || res.status == 201){
          this.$toast.success(`AdSense Count ${action} successfully!`,{
            position: "top",
            canTimeout:true,
            duration:2500,
          });
        }else{
          this.$toast.error(`Something went wrong`,{
            position: "top",
            canTimeout:true,
            duration:2500,
          });
        }
      }catch(e){
        this.$toast.error(`Something went wrong, ${e.message}`,{
          position: "top",
          canTimeout:true,
          duration:2500,
        });
      }
      }
    }
  };
</script>
<style></style>
