<template>
    <div class="card shadow bg-secondary">
    <div class="card-header">
        <div class="bg-white border-0">
        <div class="row align-items-center">
            <div class="col">
            <h3 class="mb-0">BV Generator</h3>
            </div>
        </div>
        </div>
    </div>
       <div class="card-body">
               <div class="">
                 <div class="row">
                    <div class="col-4">
                      <div class="form-group has-label">
                        <label class="form-control-label">
                          Max BV in a Day
                        </label>
                        <input type="number" class="form-control" placeholder="Max BV per day" name="max_bv_per_day" v-model="formData.max_bv_per_day">
                      </div>
                    </div>
                    <div class="col-4">
                      <div class="form-group has-label">
                        <label class="form-control-label">
                          Min Products for BV
                        </label>
                        <input type="number" class="form-control" placeholder="Mininmum Products" name="min_products" v-model="formData.min_products">
                      </div>
                    </div>
                    <div class="col-4">
                      <div class="form-group has-label">
                        <label class="form-control-label">
                          BV on Successfull Bill
                        </label>
                        <input type="number" class="form-control" placeholder="BV on Successfull bill" name="bv_on_success" v-model="formData.bv_on_success">
                      </div>
                    </div>
                 </div>
               </div>
             </div>
        <div class="card-footer">
        <div class="bg-white border-0">
        <div class="row">
            <div class="col-12 text-right">
            <button type="button" class="btn btn-info" v-on:click="updateBvGenerator()">Update</button>
            <!-- <button type="button">Add</button> -->
            </div>
        </div>
        </div>
    </div>
    </div>
</template>
<script>
import axios from "axios";

export default {
    name: "BVView",
    props: {
      hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST
      },
      source: {
        type: String,
        default:"modicare"
      },
    },
    data() {
        return {
            formData : {
                max_bv_per_day: 0,
                min_products: 0,
                bv_on_success: 0
            },
            bv_id: ''
        }
    },
    async mounted() {
        const BvGeneratorData = await axios.get(this.hostApi+'/api/bv-generate/')
        if(BvGeneratorData.data.length > 0){
            this.formData = BvGeneratorData.data[0];
            this.bv_id = BvGeneratorData.data[0].id;  
        }
    },
    methods: {
        async updateBvGenerator(){
            if(!this.bv_id)return alert("BV Id is required");
            const data = JSON.stringify(this.formData);
            const config = {
                method: 'put',
                url: this.hostApi+'/api/bv-generate/'+this.bv_id+'/',
                headers: { 
                'Content-Type': 'application/json', 
                },
                data : data
            };
            const resp = await axios(config);
            if (resp.status === 200){
                this.$toast.success(`BV Generator Data updated successfully`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
            }else{
                this.$toast.error(`Something went wrong`,{
                    position: "top",
                    canTimeout:true,
                    duration:2500,
                });
            }
        }
    }
}
</script>