<template>
    <div class="add-brand">
    <base-header type="gradient" class="pb-7 pt-7"> </base-header>
    <div class="container-fluid mt--7">
      <div class="row no-gutters justify-content-center">
        <div class="col-lg-5 mt-2">
          <span>
            <i class="fas fa-chevron-left text-info" style="outline: 0;box-shadow: none!important;">
            </i>
             <router-link :to="{ name: BackBrand }"  class="pl-2 text-info form-control-label" style="font-size: 16px;">Back</router-link>
          </span>
        </div>
      </div> 


      <div class="row no-gutters justify-content-center">
        <div class="col-lg-5 col-md-12">

          <div class="card shadow bg-secondary mb-6">
            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row align-items-center">
                  <div class="col-12">
                    <h3 class="mb-0">Edit Brand</h3>
                  </div>
                </div>
              </div>
            </div>
          <form @submit.prevent="updateBrand"> 
            <div class="card-body">
                <div class="">
                  <div class="row">
                    <div class="col-12">
                       <div class="form-group has-label">
                          <label class="form-control-label">
                             Brand Name
                          </label>
                          <input
                              type="text"
                              placeholder="Brand Name"
                              v-model="BrandName"
                              class="form-control form-control-alternative"
                              aria-describedby="addon-right addon-left"
                              required
                          />
                       </div>
                    </div>
                  </div>
                </div>
            </div>

            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row">
                  <div class="col-12 text-right">
                    <button type="button" class="btn btn-secondary mr-2" @click="resetForm()">Cancel</button>
                    <button type="submit" class="btn btn-info">Update Brand</button>
                  </div>
                </div>
              </div>
            </div>
          </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
  input{
    color: rgba(122, 23, 117, 0.616);
    font-weight: bold;
  }
</style>
<script>
import axios from "axios";
var axiosDefaults = require("axios/lib/defaults");
axiosDefaults.xsrfCookieName = "csrftoken"
axiosDefaults.xsrfHeaderName = "X-CSRFToken"
axios.defaults.xsrfHeaderName = "X-CSRFToken";

export default({
    name:"Add Brand",
    props: {
      hostApi: {
        type: String,
        default: process.env.VUE_APP_SERVER_HOST
      },
      source: {
        type: String,
        default: "modicare"
      }
    },
    data() {
       return{
           click:0,
           BrandName:'',
           BackBrand: ''
       } 
    },
    async mounted() {
      if(this.source === "modicare"){
        this.BackBrand = "Brands";
      }else{
        this.BackBrand = this.source+"Brands";
      }
      let brand_id = this.$route.params.id;
      let brand = await axios.get(this.hostApi+"/api/brand/"+ brand_id +"/");
      this.BrandName = brand.data.BrandName;
    },
    methods:{
        async updateBrand(){
            let brand_id = this.$route.params.id;
            if(!this.BrandName)return;
            var data = JSON.stringify({
                "BrandName": this.BrandName
            });
            var config = {
            method: 'put',
            url: this.hostApi+'/api/brand/'+ brand_id +'/',
            headers: { 
                'Content-Type': 'application/json',
            },
            data : data,
            xsrfHeaderName: "X-CSRFToken",
            };
            let res = await axios(config);
            if(res.status == 200){
               this.$toast.success(`BrandName updated successfully!`,{
                position: "top",
                canTimeout:true,
                duration:2500,
            });
            this.$router.push({name: this.BackBrand});
            }else{
               this.$toast.error(`Something went wrong`,{
                position: "top",
                canTimeout:true,
                duration:2500,
              });
            }
            
        }
    }
})
</script>
