<template>
  <div class="card shadow" :class="type === 'bg-default'">
    <div
      class="card-header border-0"
      :class="type ==='bg-transparent'"
    >
      <div class="row align-items-center">
        <div class="col">
          <h3 class="mb-0" :class="type === 'text-white'">
            Products of {{ CategoryName }}
          </h3>
        </div>
        <!-- <div class="col-4 mt-2"> -->
        <!-- </div> -->
      </div>
    </div>

    <div class="table-responsive">
      <base-table
        class="table align-items-center table-flush"
        :class="type === 'dark' ? 'table-dark' : ''"
        :thead-classes="type === 'dark' ? 'thead-dark' : 'thead-light'"
        tbody-classes="list"
        :data="tableData.results"
      >
        <template v-slot:columns>
          <th>Product Code</th>
          <th>Product Name</th>
          <th>Product Price</th>
          <th>Brand Name</th>
          <th>Loyalty</th>
          <th>Status</th>
          <th>Quantity</th>
          <th>Action</th>
        </template>

        <template v-slot:default="row">
          <th scope="row">
              <div class="media-body">
                <span class="name mb-0 text-sm">
                  <router-link :to="{name: ViewProduct, params:{id:row.item.product.id}}" >{{row.item.product.productCode }}</router-link>
                </span>
              </div>
          </th>
          <td class="text-align-center">
            <div class="align-center">
                {{ row.item.product.productName }}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
               Rs. {{parseInt(row.item.product.productPrice).toFixed(2)}}
            </div>
          </td>
          <td class="text-align-center">
            <div class="align-center">
                {{ row.item.product.brand.BrandName }}
            </div>
          </td>
          <td class="text-align-center">
            <div v-if="row.item.loyalty" class="text-success">True</div>
            <div v-else class="text-danger">False</div>
          </td>
          <td class="text-align-center">
            <div v-if="row.item.product.status" class="text-success">Active</div>
            <div v-else class="text-danger">InActive</div>
          </td>
           <td class="text-align-center">
            <div class="align-center">
                {{ row.item.product.quantity }}
            </div>
          </td>
          <td class="text-align-center">
            <base-dropdown class="dropdown" position="right">
               <template v-slot:title>
                <a
                  class="btn btn-sm btn-icon-only text-light"
                  role="button"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  <i class="fas fa-ellipsis-h"></i>
                </a>
              </template>
              <router-link
                class="dropdown-item"
                :to="{name:ViewProduct, params:{id: row.item.product.id}}"
                >Edit</router-link>
              <a
                class="dropdown-item"
                style="cursor: pointer"
                @click="deleteProduct(row.item.product.id)"
                >Delete</a
              >
            </base-dropdown>
          </td>
        </template>
      </base-table>
    </div>

    <div
      class="card-footer d-flex justify-content-end"
      :class="type === 'dark' ? 'bg-transparent' : ''"
    >
       <ul class="pagination">
        <li class="text-right pr-3">
          <button
            type="button"
            :disabled="!tableData.previous"
            @click="getPrevData()"
            class="pagination-btn"
          >
            <i class="fas fa-chevron-left"></i>
          </button>
        </li>
        <li class="text-center" style="max-width: 55px">
          <span class="page-number-circle">{{ pagination.default }}</span>
        </li>
        <li class="text-right pl-3">
          <button
            type="button"
            :disabled="!tableData.next"
            @click="getNextData()"
            class="pagination-btn"
          >
            <i class="fas fa-chevron-right"></i>
          </button>
        </li>
      </ul>

    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: "category-product-table",
  props: {
    type: {
      type: String,
    },
    title: String,
    hostApi: {
      type: String,
      default: process.env.VUE_APP_SERVER_HOST
    },
    source: {
      type: String,
      default: "modicare"
    },
  },
  data() {
    return {
      tableData: [],
      perpage:1,
      currentpage:1,
       pagination: {
        default: 1
      },
      brand_lists:[],
      BrandID:"",
      CategoryName:"",
      Status:"",
      Loyalty:"",
      searchQuery:"",
      ViewProduct:"",
    };
  },
  async mounted(){
      if(this.source === "modicare"){
        this.ViewProduct = "ViewProduct";
      }else{
        this.ViewProduct = this.source+"ViewProduct";
      }
      let products = await axios.get(this.hostApi+"/api/tagpivot/?category="+this.$route.params.id);
      if(products.data.count == 0){
        this.$toast.error(`No Products Found!`,{
            position: "top",
            canTimeout:true,
            duration:2500,
        });
      }
      this.tableData = products.data;
      let category = await axios.get(this.hostApi+"/api/tags/"+this.$route.params.id+"/")
      this.CategoryName = category.data.tagName;
  },
  methods:{
        async getNextData(){
          let next = this.tableData.next;
          this.tableData.next=null;
          ++this.pagination.default;
          if(next){
            let products = await axios.get(next);
            if(products.data.results.length == 0){
            this.$toast.error(`No Results Found!`,{
              position: "top",
              canTimeout:true,
              duration:2500,
            });
          }
          this.tableData = products.data;
          }
        },
      async getPrevData(){
        let prev = this.tableData.previous;
        this.tableData.previous = null
        --this.pagination.default;
        if(prev){
          let products = await axios.get(prev);
            if(products.data.results.length == 0){
            this.$toast.error(`No Results Found!`,{
              position: "top",
              canTimeout:true,
              duration:2500,
            });
          }
          this.tableData = products.data;
        }
      }
  }
};
</script>
<style>
  .pagination {
        display: flex;
        padding-left: 0;
        list-style: none;
      }
      .pagination-btn {
        background-color: #e8e5e5;
        border: 0;
        padding: 10px 40px;
        border-radius: 61px;
      }
      .page-number-circle {
        display: inline-block;
        border-radius: 50%;
        min-width: 40px;
        min-height: 40px;
        text-align: center;
        padding: 8px;
        border: 1px solid #d6d6d6;
      }

</style>
